import React, { useEffect }  from 'react'
import {Button} from './Button'
import './Footer.css';
import { Link } from 'react-router-dom';
import Aos from "aos";
import "aos/dist/aos.css";


function Footer() {
    useEffect(() =>  {
        Aos.init({ duration: 1000 });
    }, []);
    return (
        <div className='footer-container'>
            <section className="footer-subscription" data-aos='fade-up'>
                <p className="footer-subscription-heading" data-aos='fade-up'>
                    Let us transform your business now
                </p>
            </section>
            <div className="footer-links" data-aos='fade-up'>
                <div className="footer-link-wrapper">
                    <div className="footer-link-items">
                        <Link to='/'>Home</Link>
                        <Link to='/Contact'>Contact us</Link>
                        <Link to='/Services'>Services</Link>
                    </div>
                </div>
            </div>
            <section className="social-media">
                <div className="social-media-wrap">
                    <div className="footer-logo">
                        <large className="social-logo">
                        support@osau.com.au
                        </large>
                    </div>
                 
                    <div className="social-icons">
                  
                        
                    </div>
                    <small className='website-rights'>ONE SUPPORT Â© 2020</small>
                </div>
            </section>
        </div>
    )
}

export default Footer
