import React, { useEffect } from 'react';
import Aos from "aos";
import "aos/dist/aos.css";
import './PicDivider.css';


export default function PicDivider() {
    useEffect(() =>  {
        Aos.init({ duration: 1000 });
    }, []);
        return (
            <div className ='PicDivider-container'>
                <h1 className='PicDivider-h1'  data-aos='fade-up'>Servicing clients across the Sydney metropolitan region</h1> 
                 <h2 className='PicDivider-h2'  data-aos='fade-up'>Contact us now for a free consultation</h2>
            </div>
        )
    }


