import React from 'react'
import '../App.css';
import {Button} from './Button';
import './HeroSection.css';
import Typical from 'react-typical'



export default function HeroSection() {
    return (
        <div className ='hero-container'>
          {/* }  <video src="/videos/Circuit.mp4" autoPlay loop muted /> */}
            <h1>ONE SUPPORT</h1>
            <p>
                <Typical
                    loop={Infinity}
                    wrapper="c"
                    steps={[
                        'TECHNOLOGY SOLUTIONS FOR ANY BUSINESS PROBLEM',
                        1000,
                        'MANAGED SERVICE PROVIDER COVERING ALL ASPECTS OF IT',
                        1000
                   ]}
                   />
                   </p>
        </div>
    )
}
