import React, { useEffect } from 'react';
import './InfoDivider.css';
import cpuperson from './cpuperson.svg';
import Aos from "aos";
import "aos/dist/aos.css";


function InfoDivider() {
    useEffect(() =>  {
        Aos.init({ duration: 1000 });
    }, []);
        return (
            
<div class="flex-container" >
  <div class="flex-child magenta" data-aos='fade-up'>
      <h1 class='heading1'>Covering everything from infrastructure to end user devices.
      We are ready to transform your business</h1>
      <p class='paragraph'>We work with your business to understand the requirements and needs for your <b>industry</b>. Our team of experts analyse the existing systems and processes within the business 
          in order to develop a more <b>efficient</b> and <b>cost effective</b> solution.</p>
</div>
    <div class="flex-child green" data-aos='fade-up'>
     <img src= { cpuperson } className='image'/>
    </div>

</div>
     
        )
    }


    export default InfoDivider